//  ymaps.ready()  , 
//    API,      DOM-.
ymaps.ready(init);
function init(){
    var myMap = new ymaps.Map("map", {center: [52.275555, 104.281047], zoom: 12});
    function funPlacemark(geo, desc, link, phone, tel, time) {
        var myPlacemark = new ymaps.Placemark(geo, {
            balloonContent: '<a href="' + link + '" class="yandex-title"><span>' + desc + '</span></a> <a href="tel:' + phone + '" class="yandex-phone">' + tel + '</a> <div class="yandex-time">' + time + '</div>'
        } , {
            iconLayout: 'default#image',
            iconImageHref: 'images/sprites/svg/point_1.svg',
            iconImageSize: [24, 36]
        });
        myMap.geoObjects.add(myPlacemark);
    }
    myMap.behaviors.disable(['scrollZoom']);
    myMap.controls
        .remove('geolocationControl')
        .remove('searchControl')
        .remove('routeButtonControl')
        .remove('typeSelector')
        .remove('trafficControl')
        .remove('fullscreenControl')
        .remove('rulerControl');

    funPlacemark([52.265675, 104.226514], ', 3/5 ( Silver Mall, . 3)', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
    funPlacemark([52.248133, 104.269341], ', 267/3', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
    funPlacemark([52.275054, 104.256253], ', 53/3', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
    funPlacemark([52.284239, 104.288053], ', 2', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
    funPlacemark([52.284646, 104.289473], ', 4', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
    funPlacemark([52.275819, 104.305081], ', 58/1 ( ,  135)', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
    funPlacemark([52.264467, 104.329618], ', 84/2', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
    funPlacemark([52.275054, 104.256253], ', 53/3', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
    funPlacemark([52.353573, 104.153603], ', 216/1', '#', '+73952904800', '+7 (3952) 904-800', ' 10:00  20:00');
}